.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

h1 {
  color: #333;
  margin-bottom: 20px;
}

form {
  display: flex;
  margin-bottom: 20px;
}

input[type="text"] {
  padding: 8px;
  margin-right: 10px;
  font-size: 16px;
  flex-grow: 1;

}

button {
  padding: 8px;
  cursor: pointer;
  font-size: 16px;
}

select {
  margin-top: 20px;
  padding: 8px;
  font-size: 16px;
}

ul {
  list-style-type: none;
  padding: 0;
  width: 300px;
}

li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
  padding: 10px;
  background-color: #f5f5f5;
  border-radius: 5px;
}

input[type="checkbox"] {
  margin-right: 10px;
}

span {
  font-size: 18px;
  flex-grow: 1;
  text-align: left;
}

button {
  background-color: #e53935;
  color: #fff;
  border: none;
  padding: 6px;
  cursor: pointer;
}

button:hover {
  background-color: #d32f2f;
}

div p {
  font-size: 16px;
  margin: 5px 0;
}
